import React, { useEffect } from "react";

const SearchBox = ({ searchQuery, setSearchQuery, onSelect, astromons }) => {
  // This effect runs when the user presses Enter
  const handleKeyPress = (e) => {
    if (e.key === "Enter") {
      const selectedAstromon = Object.entries(astromons).find(([slug, names]) =>
        names
          .slice(1)
          .some((name) =>
            name.toLowerCase().includes(searchQuery.toLowerCase())
          )
      );
      if (selectedAstromon) {
        const slug = selectedAstromon[0]; // Get the slug of the first matching astromon
        onSelect(slug); // Call the onSelect function with the slug
      }
    }
  };

  return (
    <input
      type="text"
      value={searchQuery}
      onChange={(e) => setSearchQuery(e.target.value)}
      placeholder="Search Astromon"
      onKeyPress={handleKeyPress} // Handle Enter key press
      style={{
        width: "100%",
        padding: "10px",
        marginBottom: "20px",
        borderRadius: "5px",
      }}
    />
  );
};

export default SearchBox;
