import React from "react";

const StatsGrid = ({ stats }) => {
  return (
    <div
      className="stats-grid"
      style={{
        display: "grid",
        gridTemplateColumns: "repeat(2, 1fr)",
        gap: "10px",
      }}
    >
      <div>HP: {stats.hp}</div>
      <div>ATK: {stats.atk}</div>
      <div>DEF: {stats.def}</div>
      <div>REC: {stats.rec}</div>
    </div>
  );
};

export default StatsGrid;
