import React, { useState } from "react";

const FAQ = () => {
  const [isFaqVisible, setIsFaqVisible] = useState(false); // State for FAQ visibility

  const toggleFaq = () => {
    setIsFaqVisible((prev) => !prev); // Toggle visibility of the FAQ
  };

  return (
    <div>
      {/* FAQ Button */}
      <button
        className="faq-button"
        onClick={toggleFaq}
        style={{
          position: "fixed",
          bottom: "20px",
          right: "20px",
          padding: "10px",
          backgroundColor: "#4CAF50",
          color: "white",
          border: "none",
          borderRadius: "5px",
          cursor: "pointer",
        }}
      >
        FAQ
      </button>

      {/* FAQ Section */}
      {isFaqVisible && (
        <div className="faq-container" style={{ marginTop: "20px" }}>
          <h3>Frequently Asked Questions</h3>
          <ul>
            <li>What is this tool?</li>
            <li>How do I use the character builder?</li>
            <li>Can I save my progress?</li>
          </ul>
          <button onClick={toggleFaq} style={{ marginTop: "10px" }}>
            Close
          </button>
        </div>
      )}
    </div>
  );
};

export default FAQ;
