import React from "react";

const EvolutionIcons = ({ onEvolutionSelect }) => {
  const evolutions = ["evolution1", "evolution2", "evolution3", "evolution4"];

  return (
    <div
      className="evolution-icons"
      style={{
        display: "flex",
        justifyContent: "space-between",
        margin: "20px 0",
      }}
    >
      {evolutions.map((evolution, index) => (
        <img
          key={index}
          src={`/path-to-evolution-icons/${evolution}.png`}
          alt={`Evolution ${index + 1}`}
          style={{ width: "50px", height: "50px", cursor: "pointer" }}
          onClick={() => onEvolutionSelect(index + 1)} // Call the onEvolutionSelect with the index
        />
      ))}
    </div>
  );
};

export default EvolutionIcons;
