import React from "react";

const SelectedAstromon = ({ selectedName, selectedData }) => {
  // Extract relevant data from selectedData if available
  const { icon, element, slug } = selectedData || {};

  return (
    selectedName && (
      <div className="selected-name">
        {icon && (
          <img
            src={icon} // Use the icon URL directly
            alt="Element Icon"
            onClick={() => console.log("Change Element Functionality")}
            style={{
              cursor: "pointer",
              width: "30px",
              height: "30px",
              marginRight: "10px",
            }}
          />
        )}
        <span>{slug || selectedName}</span> {/* Show the slug or name */}
      </div>
    )
  );
};

export default SelectedAstromon;
