import React, { useState, useEffect } from "react";
import SearchBox from "../SearchBox";
import SelectedAstromon from "../SelectedAstromon";
import EvolutionIcons from "../EvolutionIcons";
import StatsGrid from "../StatsGrid";
import FilteredResults from "../FilteredResults";
import SkillBoxes from "../SkillBoxes";

const AstromonBaseCard = ({ onSelect }) => {
  const [astromons, setAstromons] = useState({});
  const [searchQuery, setSearchQuery] = useState("");
  const [filteredResults, setFilteredResults] = useState([]);
  const [selectedAstromon, setSelectedAstromon] = useState(null); // Store the selected Astromon data

  useEffect(() => {
    const fetchAstromons = async () => {
      try {
        const response = await fetch("./data/astromon-families.json");
        const data = await response.json();
        setAstromons(data);
      } catch (error) {
        console.error("Error loading astromon data:", error);
      }
    };

    fetchAstromons();
  }, []);

  useEffect(() => {
    const results = Object.entries(astromons)
      .filter(([slug, names]) => {
        return (
          slug.includes(searchQuery.toLowerCase()) ||
          names
            .slice(1)
            .some((name) =>
              name.toLowerCase().includes(searchQuery.toLowerCase())
            )
        );
      })
      .map(([slug, names]) => {
        return `${names[1]} -> ${names[2]} -> ${names[3]}`;
      })
      .slice(0, 5);

    setFilteredResults(results);
  }, [searchQuery, astromons]);

  const handleSelect = (slug) => {
    if (onSelect) {
      onSelect(slug);
      // Retrieve the selected Astromon data from astromons
      const selectedData = astromons[slug];
      setSelectedAstromon(selectedData); // Store the selected Astromon data
      setSearchQuery(""); // Clear the search box
      setFilteredResults([]); // Collapse the filtered results
    }
  };

  return (
    <div className="tool-card">
      <SearchBox searchQuery={searchQuery} setSearchQuery={setSearchQuery} />
      <SelectedAstromon
        selectedName={selectedAstromon ? selectedAstromon[1] : ""}
      />{" "}
      {/* Update to show the selected Astromon's name */}
      <EvolutionIcons />
      <StatsGrid />
      <FilteredResults
        filteredResults={filteredResults}
        handleSelect={handleSelect}
      />
      <SkillBoxes selectedAstromon={selectedAstromon} />{" "}
      {/* Pass the selected Astromon to SkillBoxes */}
    </div>
  );
};

export default AstromonBaseCard;
