import React from "react";

const SkillBoxes = ({ selectedAstromon }) => {
  // This is a placeholder for actual skill data.
  const skills = selectedAstromon
    ? selectedAstromon.skills
    : ["Skill 1", "Skill 2", "Skill 3", "Skill 4"];

  return (
    <div
      className="skill-boxes"
      style={{
        display: "flex",
        justifyContent: "space-between",
        marginTop: "20px",
      }}
    >
      {skills.map((skill, index) => (
        <div
          key={index}
          style={{
            width: "50px",
            height: "50px",
            border: "1px solid #000",
            borderRadius: "5px",
            textAlign: "center",
            lineHeight: "50px",
            cursor: "pointer",
          }}
          onClick={() => console.log(`Select Skill ${index + 1}`)} // Replace with your skill selection function
        >
          {skill}
        </div>
      ))}
    </div>
  );
};

export default SkillBoxes;
